
.view-visit-details {
  padding-top: 1.01852rem;
  overflow: hidden;
  background: #e3e3e5;
}
.view-visit-details .box-body {
  background: #fff;
  padding: 0.27778rem 0.55556rem;
  position: relative;
}
.view-visit-details .box-details {
  border: 0.00926rem solid #d4d4d4;
  background: #fff;
  border-radius: 0.13889rem;
  width: 8.33333rem;
  margin: 0 auto;
  margin-top: 0.55556rem;
  padding: 0.27778rem 0.55556rem;
}
.view-visit-details .box-details p {
  font-size: 0.38889rem;
  word-break: break-all;
  min-height: 0.74074rem;
}
.view-visit-details .box-details .box-footer {
  font-size: 0.35185rem;
  text-align: right;
  color: #989898;
  margin-top: 0.37037rem;
}
.view-visit-details .comment {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.view-visit-details .comment .vux-rater {
  margin-top: 0.27778rem;
}
.view-visit-details .comment textarea {
  font-size: 0.38889rem;
}
.view-visit-details .comment button {
  width: 100%;
  border-radius: 0;
}



