.view-face-recognition .form {
  width: 92%;
  margin: 0 auto;
  margin-top: 0.37037rem;
}
.view-face-recognition .form .title {
  text-align: center;
  font-size: 0.38889rem;
  margin-bottom: 0.37037rem;
}
.view-face-recognition .form input {
  width: 100%;
  height: 1.11111rem;
  background: #f3f1f4;
  border: 0.00926rem solid #d9d7da;
  font-size: 0.38889rem;
  text-indent: 1em;
  margin-bottom: 0.37037rem;
}
.view-face-recognition .form .id-card {
  margin-bottom: 0.18519rem;
  margin-top: 0.18519rem;
}
.view-face-recognition .form .id-card .demo-container {
  width: 100%;
  height: 100%;
}
.view-face-recognition .form .id-card .demo-container .rect {
  border: 0.01852rem solid #a64ceb;
  left: -9.25926rem;
  position: absolute;
  top: -9.25926rem;
}
.view-face-recognition .form .id-card .img {
  float: left;
  text-align: center;
  width: 100%;
  height: 5.55556rem;
  background: #f3f1f4;
  position: relative;
  border: 0.00926rem dashed #d8d8da;
  padding: 0.46296rem 0;
}
.view-face-recognition .form .id-card .img .id-card-img {
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
}
.view-face-recognition .form .id-card .img .id-card-img img {
  width: auto;
  height: 100%;
}
.view-face-recognition .form .id-card .img p {
  font-size: 0.38889rem;
}
.view-face-recognition .form .id-card .img input {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  opacity: 0;
  z-index: 3;
}
.view-face-recognition .form .id-card .img:first-child {
  margin-right: 2%;
}
.view-face-recognition .form .id-card .face-text {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -13%;
  margin-left: -8%;
}
.view-face-recognition .form .msg {
  margin-top: 0.55556rem;
  background: url(~assets/images/add-real-name-msg.png) no-repeat 0.50926rem;
  background-size: contain;
  padding-left: 0.64815rem;
  font-size: 0.38889rem;
  text-align: center;
}
.view-face-recognition .form button {
  border-radius: 0.13889rem;
  font-size: 0.48148rem;
}
.view-face-recognition .state-box {
  text-align: center;
  margin-top: 1.11111rem;
}
.view-face-recognition .state-box .img {
  width: 2.12963rem;
  height: 2.12963rem;
  margin: 0 auto;
  margin-bottom: 0.18519rem;
}
.view-face-recognition .state-box .img img {
  width: 100%;
}
.view-face-recognition .state-box p {
  font-size: 0.38889rem;
}
.view-face-recognition .state-box .danger p {
  margin-bottom: 0.74074rem;
}
.view-face-recognition .state-box .danger button {
  font-size: 0.38889rem;
  padding: 0.09259rem 0;
  width: 90%;
  border-radius: 0.13889rem;
  margin: 0 auto;
}



