
.view-appointment {
  height: 100%;
  background: #fff;
}
.view-appointment .common-header-right .img-btn {
  position: absolute;
  right: 1.11111rem;
}
.view-appointment .visit-state {
  right: 1.11111rem;
  position: fixed;
  width: 1.38889rem;
  height: 0.92593rem;
  z-index: 999;
}
.view-appointment .visit-state .weui-cell_access:active {
  background: none;
}
.view-appointment .visit-state .weui-cell {
  height: 0.92593rem;
}
.view-appointment .visit-state .weui-cell .weui-cell__ft {
  display: none;
}
.view-appointment .visit-state .vux-popup-picker-select {
  display: none;
}
.view-appointment .list ul li {
  position: relative;
  padding: 0.37037rem 0;
  /*margin-bottom: 90px;*/
  border-bottom: 0.01852rem solid #d4d4d4;
  width: 100%;
  float: left;
}
.view-appointment .list ul li .img {
  margin-left: 0.46296rem;
  width: 1.57407rem;
  height: 1.57407rem;
  overflow: hidden;
  border-radius: 100%;
  border: 0.01852rem solid #fff;
  float: left;
  margin-top: 0.18519rem;
}
.view-appointment .list ul li .img img {
  width: 100%;
  height: 100%;
}
.view-appointment .list ul li .text {
  float: left;
  margin-left: 0.37037rem;
}
.view-appointment .list ul li .text h1 {
  font-size: 0.51852rem;
}
.view-appointment .list ul li .text h1 span {
  background: #00aeff;
  color: #fff;
  padding: 0.0463rem 0.18519rem;
  border-radius: 0.09259rem;
  font-size: 0.31481rem;
  margin-left: 0.09259rem;
}
.view-appointment .list ul li .text p {
  font-size: 0.38889rem;
}
.view-appointment .list ul li .text p.time {
  color: #808080;
}
.view-appointment .list ul li .state {
  position: absolute;
  right: 0;
  top: 0;
  width: 1.85185rem;
  height: 1.85185rem;
}
.view-appointment .list ul li .state.overdue {
  background: url(~assets/images/appointment-state-overdue.png) no-repeat;
  background-size: contain;
}
.view-appointment .list ul li .state.success {
  background: url(~assets/images/appointment-state-success.png) no-repeat;
  background-size: contain;
}
.view-appointment .list ul li .state.warn {
  background: url(~assets/images/appointment-state-warn.png) no-repeat;
  background-size: contain;
}
.view-appointment .list ul li .state.cancel {
  background: url(~assets/images/appointment-state-cancel.png) no-repeat;
  background-size: contain;
}
.view-appointment .list ul li .bottom {
  background: #e64340;
  color: #fff;
  padding: 0.18519rem 0;
  margin-top: 0.27778rem;
  float: left;
  width: 100%;
  text-align: center;
  font-size: 0.38889rem;
}



