
.view-member-family {
  position: relative;
}
.view-member-family .weui-dialog {
  min-height: 200px;
  max-height: 900px;
  overflow: scroll;
  background: #fff;
}
.view-member-family .list .no-data {
  font-size: 0.38889rem;
  margin-top: 0.64815rem;
  display: block;
}
.view-member-family li {
  border-bottom: 0.01852rem solid #9a9a9a;
  padding: 0.37037rem 0;
}
.view-member-family li:last-child {
  border: none;
}
.view-member-family li .common-box-list .text {
  width: 5.09259rem;
  text-align: left;
}



