
.view-hospital {
  padding-top: 1.01852rem;
}
.view-hospital .list {
  min-height: 1.85185rem;
  background: #fff;
}
.view-hospital .list ul li {
  padding: 0.37037rem 0;
  background: #fff;
  float: left;
  width: 100%;
  border-bottom: 0.01852rem solid #d7d7d7;
}
.view-hospital .list ul li:last-child {
  border: none;
}
.view-hospital .list ul li .img {
  margin-left: 0.37037rem;
  float: left;
  width: 3.24074rem;
  height: 2.31481rem;
  overflow: hidden;
}
.view-hospital .list ul li .img img {
  width: 100%;
  height: 100%;
}
.view-hospital .list ul li .text {
  margin-right: 0.37037rem;
  margin-top: 0.18519rem;
  width: 5.55556rem;
  float: left;
  margin-left: 0.37037rem;
}
.view-hospital .list ul li .text h1 {
  font-size: 0.48148rem;
  width: 100%;
  overflow: hidden;
  -webkit-line-clamp: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.view-hospital .list ul li .text h1 span {
  color: #f76301;
  float: right;
}
.view-hospital .list ul li .text p {
  font-size: 0.38889rem;
  color: #989898;
  overflow: hidden;
  -webkit-line-clamp: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.view-hospital .list ul li .text p span {
  color: #f76301;
}
.view-hospital .list ul li .text p.address span {
  color: #000;
}



