
.view-feedback {
  padding-top: 1.01852rem;
  height: 94.3%;
  background: #fff;
}
.view-feedback .message {
  margin-top: 0.46296rem;
  margin-bottom: 0.64815rem;
  font-size: 0.38889rem;
  text-align: center;
}
.view-feedback .group {
  width: 90%;
  margin: 0 auto;
  margin-bottom: 0.37037rem;
}
.view-feedback .group .title {
  font-size: 0.38889rem;
  border-bottom: 0.00926rem solid #d3d3d3;
  padding-bottom: 0.18519rem;
  margin-bottom: 0.27778rem;
}
.view-feedback .group ul li {
  display: inline-block;
  width: 2.12963rem;
  border: 0.00926rem solid #dfdfdf;
  padding: 0.18519rem;
  text-align: center;
  margin-bottom: 0.18519rem;
  font-size: 0.38889rem;
  margin-right: 0.37037rem;
}
.view-feedback .group ul li:last-child {
  margin-right: 0;
}
.view-feedback .group ul li.active {
  border-color: #00aeff;
  background: url(~assets/images/feedback.png) no-repeat right top;
  background-size: 25%;
}
.view-feedback .form .weui-cells:after,
.view-feedback .form .weui-cell:after,
.view-feedback .form .weui-cells:before,
.view-feedback .form .weui-cell:before {
  border: none;
}
.view-feedback .form .vux-x-textarea {
  border-radius: 0.13889rem;
  background: #f3f1f4;
  border: 0.00926rem solid #d9d7da;
}
.view-feedback .form .vux-x-textarea textarea {
  font-size: 0.38889rem;
  background: #f3f1f4;
}
.view-feedback .form .vux-x-input {
  border: 0.00926rem solid #d9d7da;
  margin-top: 0.27778rem;
  background: #f3f1f4;
  margin-bottom: 0.55556rem;
}
.view-feedback .form button {
  padding: 0.09259rem 0;
  border-radius: 0.13889rem;
}
.view-feedback .state-box {
  margin-top: 0.74074rem;
}
.view-feedback .state-box .success .img {
  width: 2.12963rem;
  height: 2.12963rem;
  margin: 0 auto;
  margin-bottom: 0.27778rem;
}
.view-feedback .state-box .success .img img {
  width: 100%;
}
.view-feedback .state-box .success p {
  text-align: center;
  font-size: 0.38889rem;
}
.view-feedback .state-box .success p span {
  color: #ec6700;
}



