.vux-timeline {
  padding: 1rem;
}
.vux-timeline > ul > li {
  list-style: none;
}
.vux-timeline-item {
  position: relative;
}
.vux-timeline-item-content {
  padding: 0 0 1.5rem 1.2rem;
}
.vux-timeline-item-head,
.vux-timeline-item-head-first {
  position: absolute;
  content: '';
  z-index: 99;
  border-radius: 0.91667rem;
}
.vux-timeline-item-head {
  width: 0.09259rem;
  height: 0.09259rem;
  left: 0.00926rem;
  top: 0.03704rem;
}
.vux-timeline-item-head-first {
  width: 0.18519rem;
  height: 0.18519rem;
  left: -0.03704rem;
  top: 0.0463rem;
}
.vux-timeline-item-tail {
  position: absolute;
  content: '';
  height: 100%;
  width: 0.01852rem;
  left: 0.0463rem;
  top: 0.0463rem;
  background-color: #04BE02;
}
.vux-timeline-item-checked {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 0.18519rem;
  height: 0.18519rem;
}
.vux-timeline-item-checked.weui-icon-success-no-circle::before {
  font-size: 0.12963rem;
  position: absolute;
  left: 0.02778rem;
  top: 0.02778rem;
  margin: 0!important;
  color: #FFF;
}
.vux-timeline-item-color {
  background-color: #04BE02;
}



